.cardContainer {
    padding: 3%;
}

.flex-center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: white;
}

.flexContainer {
    display: -ms-flexbox;
    display: flex
}

.Typist .Cursor {
    display: inline-block;
    color: white;
    font-size: 45px;}
.Typist .Cursor--blinking {
    opacity: 1;
    -webkit-animation: blink 1s linear infinite;
            animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
    0% {
        opacity: 1; }
    50% {
        opacity: 0; }
    100% {
        opacity: 1; } }

@keyframes blink {
    0% {
        opacity: 1; }
    50% {
        opacity: 0; }
    100% {
        opacity: 1; } }
@media (max-width: 768px) {
    .cardContainer {
        padding: 3% 0 3% 0;
    }

    .pieChartLabel {
        font-size: 0.55rem;
    }
}




